import { NgModule, ModuleWithProviders } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';

import { FooterConfigurationService } from './services/configuration.service';
import { DatatablePagerComponent } from './datatable-pager/datatable-pager.component';
import { DatatableFooterComponent } from './datatable-footer/datatable-footer.component';

@NgModule({
  imports: [
    CommonModule,
    FormsModule
  ],
  declarations: [
    DatatablePagerComponent,
    DatatableFooterComponent
  ],
  exports: [
    DatatablePagerComponent,
    DatatableFooterComponent
  ],
  providers: [
    FooterConfigurationService
  ]
})
export class NgxDatatableFooterModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: NgxDatatableFooterModule,
    };
  }
}
