import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  AfterViewInit,
  ViewChild,
  ElementRef,
  OnDestroy,
} from "@angular/core";

import { debounceTime } from "rxjs/operators";
import { fromEvent, Subscription } from "rxjs";

@Component({
  selector: "app-datatable-pager",
  template: ` <ul class="pager">
    <li
      class="page-size-continer"
      *ngIf="linage"
      (click)="pageSizeColumn = !pageSizeColumn"
      (mouseleave)="pageSizeColumn = true"
    >
      <span>{{ size }} {{ labels.line }}/{{ labels.page }}</span>
      <span
        class="fa"
        [ngClass]="{
          'fa-angle-up': pageSizeColumn,
          'fa-angle-down': !pageSizeColumn
        }"
      ></span>
      <ul [hidden]="pageSizeColumn" class="page-size-content">
        <li
          *ngFor="let ps of pageSizes"
          (click)="changePageSize(ps)"
          [ngClass]="{ 'page-size-column-active': size === ps }"
        >
          {{ ps }} {{ labels.line }}/{{ labels.page }}
        </li>
      </ul>
    </li>
    <li *ngIf="displayBothEnds" [class.disabled]="!canPrevious()">
      <a (click)="firstPage()">
        <i class="{{ pagerPreviousIcon }}"></i>
      </a>
    </li>
    <li [class.disabled]="!canPrevious()">
      <a (click)="prevPage()">
        <i class="{{ pagerLeftArrowIcon }}"></i>
      </a>
    </li>
    <li
      class="pages"
      *ngFor="let pg of pages"
      [class.active]="pg.number === page"
    >
      <a *ngIf="pageNumber" (click)="selectPage(pg.number)">
        {{ pg.text }}
      </a>
    </li>
    <li [class.disabled]="!canNext()">
      <a (click)="nextPage()">
        <i class="{{ pagerRightArrowIcon }}"></i>
      </a>
    </li>
    <li *ngIf="displayBothEnds" [class.disabled]="!canNext()">
      <a (click)="lastPage()">
        <i class="{{ pagerNextIcon }}"></i>
      </a>
    </li>
    <li [ngStyle]="{ position: 'relative' }">
      <span>{{ labels.jumpTo }}</span>
      <input
        #customerInput
        class="lz-jump-page"
        type="number"
        title=" "
        rows="2"
        [min]="1"
        [ngModel]="page"
        (keydown)="keyDownHandel($event)"
        (blur)="blurHandel()"
      />{{ labels.page }}
    </li>
  </ul>`,
  styles: [
    `
      .page-size-continer {
        position: relative;
        min-width: 80px;
        height: 26px;
        line-height: 26px;
        text-align: center;
        border-radius: 2px;
        cursor: pointer;
        padding: 0 10px !important;
      }
      .page-size-continer:hover {
        background-color: rgba(158, 158, 158, 0.2);
      }
      .page-size-continer .page-size-content {
        position: absolute;
        bottom: 24px;
        left: 0;
        padding: 0;
        background: #fff;
        border-radius: 4px;
        box-shadow: 0 0 15px 1px rgba(113, 106, 202, 0.2);
        z-index: 11;
      }
      .page-size-continer .page-size-content li {
        width: 100%;
        padding: 4px 0;
      }
      .page-size-continer .page-size-content li:hover {
        background-color: rgba(158, 158, 158, 0.2);
      }
      .page-size-continer .page-size-content .page-size-column-active {
        color: #409eff;
      }
      input[type="number"]::-webkit-inner-spin-button,
      input[type="number"]::-webkit-outer-spin-button {
        -webkit-appearance: none;
      }
      .lz-jump-page {
        display: inline-block;
        width: 50px;
        border-radius: 2px;
        margin-right: 6px;
        padding-left: 4px;
        border: solid 1px #ebedf2;
      }
      input[type="number"] {
        -moz-appearance: textfield;
      }
    `,
  ],
})
export class DatatablePagerComponent
  implements OnInit, AfterViewInit, OnDestroy {
  @Input() pagerLeftArrowIcon: string;
  @Input() pagerRightArrowIcon: string;
  @Input() pagerPreviousIcon: string;
  @Input() pagerNextIcon: string;
  @Input() pageSizes: any[];
  @Input() datatable: any;
  @Input() linage?: Boolean = true;
  @Input() displayBothEnds?: Boolean = true;
  @Input() pageNumber?: Boolean = true;
  @Input() pageJumpTime?: number = 1000;
  @Input() labels: any;

  @Input()
  set size(val: number) {
    this._size = val;
    this.pages = this.calcPages();
  }
  get size(): number {
    return this._size;
  }

  @Input()
  set count(val: number) {
    this._count = val;
    this.pages = this.calcPages();
  }
  get count(): number {
    return this._count;
  }

  @Input()
  set page(val: number) {
    if (val) {
      this._page = val;
      this.pages = this.calcPages();
    }
  }
  get page(): number {
    return this._page;
  }
  get totalPages(): number {
    const count = this.size < 1 ? 1 : Math.ceil(this.count / this.size);
    return Math.max(count || 0, 1);
  }

  @Output() change: EventEmitter<any> = new EventEmitter();
  @Output() pageChange: EventEmitter<any> = new EventEmitter();
  @Output() maxPage: EventEmitter<any> = new EventEmitter();

  currentOffset: number;
  prveOffset: number;
  inputEvent: Subscription;

  @ViewChild("customerInput")
  customerInput: ElementRef;

  ngOnInit() {}

  ngAfterViewInit() {
    this.inputEvent = fromEvent(this.customerInput.nativeElement, "input")
      .pipe(debounceTime(this.pageJumpTime))
      .subscribe((event: any) => {
        let value: number;
        if (event.target.value) {
          value = Number(event.target.value);
          if (value < 1) {
            value = 1;
            this.customerInput.nativeElement.value = 1;
          } else if (value > this.totalPages) {
            value = this.totalPages;
            this.customerInput.nativeElement.value = this.totalPages;
            this.maxPage.emit(this.totalPages);
          }
          this.page = value;
          this.selectPage(Math.round(value));
        }
      });
  }

  // tslint:disable-next-line:member-ordering
  _page = 1;
  // tslint:disable-next-line:member-ordering
  _count = 0;
  // tslint:disable-next-line:member-ordering
  _size = 0;
  // tslint:disable-next-line:member-ordering
  pages: any;
  // tslint:disable-next-line:member-ordering
  pageSizeColumn: boolean = true;

  canPrevious(): boolean {
    return this.page > 1;
  }

  canNext(): boolean {
    return this.page < this.totalPages;
  }

  prevPage(): void {
    this.selectPage(this.page - 1);
  }

  nextPage(): void {
    this.selectPage(this.page + 1);
  }

  firstPage(): void {
    if (this.canPrevious()) {
      this.selectPage(1);
    }
  }

  lastPage(): void {
    if (this.canNext()) {
      this.selectPage(this.totalPages);
    }
  }

  selectPage(page: number, changePageSize?: any): void {
    if (changePageSize) {
      if (page > 0 && page <= this.totalPages) {
        this.page = page;
        this.currentOffset = page;
        this.change.emit({ page });
      }
    } else {
      if (page > 0 && page <= this.totalPages) {
        this.page = page;
        this.currentOffset = page;
        this.change.emit({ page });
      }
    }
  }

  calcPages(page?: number): any[] {
    const pages = [];
    let startPage = 1;
    let endPage = this.totalPages;
    const maxSize = 5;
    const isMaxSized = maxSize < this.totalPages;

    page = page || this.page;

    if (isMaxSized) {
      startPage = (Math.ceil(page / maxSize) - 1) * maxSize + 1;
      endPage = Math.min(startPage + maxSize - 1, this.totalPages);
    }

    for (let num = startPage; num <= endPage; num++) {
      pages.push({ number: num, text: num });
    }

    return pages;
  }

  blurHandel() {
    this.customerInput.nativeElement.value = this.page;
  }

  changePageSize(pageSize: number) {
    this.datatable.limit = pageSize;
    this.selectPage(1, "changePageSize");
  }

  keyDownHandel(event: any) {
    if (this.customerInput.nativeElement.value) {
      this.prveOffset = Number(this.customerInput.nativeElement.value);
    }

    if (
      (event.keyCode < 48 && event.keyCode !== 8) ||
      (event.keyCode > 57 && event.keyCode < 96) ||
      event.keyCode > 105
    ) {
      return false;
    }
  }

  ngOnDestroy() {
    this.inputEvent.unsubscribe();
  }
}
